@mixin buttonDowanload {
  display: inline-block;
  width: 100px;
  height: 32px;
  line-height: 32px;
  padding-left: 40px;
  text-decoration: none;
  margin-bottom: 14px;
  border-radius: 5px;
  font-size: 13px;
  color: #00B548;
  background-color: #ffffff;
  background-image: url(../../assets/icon-download.png);
  background-image: image-set(url(../../assets/icon-download.png) 1x, url(../../assets/icon-download@2x.png) 2x );
  background-repeat: no-repeat;
  background-size: 18px;
  background-position: 18px 7px;
}

.downloadContainer {
  p {
    color: #ffffff;
  }

  display: flex;
  flex-direction: column;
  align-items: center;
  flex-shrink: 0;
  margin: 18px 0 0 35px;
  padding: 22px 25px;
  background: rgba(0, 136, 54, .164);

  .downloadButton {
    @include buttonDowanload;
    margin-top: 7px;
  }

  .downloadPlatform {
    a {
      position: relative;
      display: inline-block;
      text-decoration: none;
      cursor: pointer;
      padding: 0 8px 0 7px;

      &:not(:first-child) {
        &::before {
          content: '';
          display: block;
          position: absolute;
          top: 5px;
          left: 0;
          width: 1px;
          height: 12px;
          background: #ffffff;
          margin-right: 5px;
        }
      }

      // &:hover .qrContent {
      //   opacity: 1;
      //   transform: scale(1);
      // }

      span {
        color: #ffffff;
        font-size: 12px;
      }
      
      .qrContent {
        cursor: auto;
        width: 120px;
        height: 149px;
        position: absolute;
        left: 50%;
        margin-left: -60px;
        padding-top: 25px;
        text-align: center;
        transform: scale(0, 0);
        opacity: 0;
        transition: all .3s ease;
        background-image: url(../../assets/popover-background.png);
        background-image: image-set(url(../../assets/popover-background.png) 1x, url(../../assets/popover-background@2x.png) 2x );
        background-size: 120px 168px;
        background-repeat: no-repeat;
        background-position-y: -5px;

        &.show {
          opacity: 1;
          transform: scale(1);
        }
  
        img {
          width: 70px;
          height: 70px;
        }
  
        p {
          transform: scale(.9);
          transform-origin: center;
          color: #616161;
        }
      }
    }
  }
}

.downloadButtonMobile {
  @include buttonDowanload;
  display: none;
  width: 100px;
  margin-top: 24px;
}

@media screen and (max-width: 768px) {
  .downloadContainer {
    display: none;
  }

  .downloadButtonMobile {
    display: block;
  }
}


