@import '../../globalVariable';

body {
  background: #ffffff;
}

.homeContainer {
  p {
    color: #616161;
    font-size: 12px;
    line-height: 20px;
  }
  
  .brandIntro {
    padding: 48px;
    background: url(../../assets/lark_background.png);
    display: flex;
    justify-content: space-between;

    p {
      color: #ffffff;
    }

    .introLeft {
      .headerLogo {
        width: 138px;
        height: 38px;
        margin-bottom: 24px;
        background-image: url(../../assets/logo.png);
        background-image: image-set(url(../../assets/logo.png) 1x, url(../../assets/logo@2x.png) 2x );
        background-repeat: no-repeat;
      }

      .introBoldText {
        font-weight: bold;
        margin-bottom: 12px; 
      }
    }
  }

  .bindContent {
    padding: 34px 48px;

    p {
      font-size: 13px;
    }

    .bindAndAuth {
      background-image: url(../../assets/bind-background.png);
      background-image: image-set(url(../../assets/bind-background.png) 1x, url(../../assets/bind-background@2x.png) 2x );
      background-repeat: no-repeat;
      background-position-x: right;
      background-size: contain;

      h3 {
        font-size: 15px;
        margin-bottom: 0 0 16px 0;
      }

      p {
        margin-top: 10px;
      }
  
      .bindButton {
        @include publicBindButton;
        width: 200px;
        background: #00B548;
      }
  
      .unbindButton {
        @include publicBindButton;
        border: none;
        min-width: 160px;
        background: #F56453;
      }
    }

    .bindText {
      background: #F7F7F7;
      margin-top: 40px;
      padding: 14px 20px;

      p {
        color: #898989;
        line-height: 22px;

        &.bindLabel {
          margin-bottom: 6px;
          padding-left: 18px;
          background-image: url(../../assets/icon-info.png);
          background-image: image-set(url(../../assets/icon-info.png) 1x, url(../../assets/icon-info@2x.png) 2x );
          background-repeat: no-repeat;
          background-position-y: 4px;
        }
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .homeContainer {
    .brandIntro {
      flex-direction: column;
      padding: 32px 24px;
    }

    .bindContent {
      padding: 24px;
    }
  }
}
